:root {
  --primary-color: #38a39e;
}

.App {
  text-align: center;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
body {
  font-family: Arial, Helvetica, sans-serif;
  color: #333;
  line-height: 1.6;
  margin-bottom: 10px;
}

ul {
  list-style-type: none;
}
a {
  text-decoration: none;
  color: #333;
}
h1 {
  font-weight: 300;
  line-height: 1.2;
  margin: 10px 0px;
}
h2 {
  font-weight: 400;
  line-height: 1.2;
  margin: 10px 0px;
}
.logo a span {
  padding-left: 0.5em;
}
