img {
  width: 100%;
}

.navbar .flex {
  justify-content: space-between;
}
.navbar {
  background-color: var(--primary-color);
  color: #fff;
  height: 70px;
}

.navbar ul {
  display: flex;
}

.navbar a {
  color: #fff;
  padding: 10px;
  margin: 0 5px;
}

.navbar nav a:hover {
  border-bottom: 2px white solid;
}

/* Showcase */

.showcase {
  background-color: var(--primary-color);
  color: #fff;
  position: relative;
  height: 400px;
}
.showcase h1 {
  font-size: 40px;
}
.showcase p {
  margin: 20px 0;
}

.showcase .grid {
  grid-template-columns: 55% 45%;
  gap: 30px;
  overflow: visible;
}
.showcase-form {
  position: relative;
  top: 60px;
  height: auto;
  width: auto;
  padding: 40px;
  z-index: 10;
}

.showcase-form .form-control {
  margin: 30px 0;
}

.showcase-form input:focus {
  outline: none;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0 30px white inset !important;
  box-shadow: 0 0 0 30px white inset !important;
}
.showcase-form input[type='text'],
.showcase-form input[type='email'],
.showcase-form input[type='password'],
.showcase-form input[type='date'] {
  border: 0;
  border-bottom: 1px solid #b4becb;
  width: 100%;
  padding: 3px;
  font-size: 16px;
}

.showcase-form button:disabled {
  background: #dddddd;
}
.form-control .form-error {
  color: red;
}
.error {
  color: red;
}
.showcase::before,
.showcase::after {
  content: '';
  position: absolute;
  height: 100px;
  bottom: -70px;
  right: 0;
  left: 0;
  background: #fff;
  transform: skewY(-2deg);
  box-shadow: 0 -5px 5px rgba(255, 255, 255, 0.6);
}

/* Login */

.login {
  position: relative;
  height: 50vh;
  background: var(--primary-color);
  z-index: 100;
}

.login-form {
  position: relative;
  top: 10px;
  min-height: 300px;
  width: 400px;
  padding: 20px;
}

.login-form .form-control {
  margin: 20px 0;
}

.login-form input:focus {
  outline: none;
}
.login-form a {
  color: #38a39e;
  font-weight: bold;
}
.login-form a:visited {
  color: #38a39e;
}
.login-form h3 {
  margin: 0 0 20px 0;
}
.login-form input[type='text'],
.login-form input[type='password'] {
  border: 0;
  border-bottom: 1px solid #b4becb;
  width: 100%;
  padding: 3px;
  font-size: 16px;
}
.login::before,
.login::after {
  content: '';
  position: absolute;
  height: 100px;
  bottom: -70px;
  right: 0;
  left: 0;
  background: #fff;
  transform: skewY(-2deg);
  box-shadow: 0 -5px 5px rgba(255, 255, 255, 0.6);
  z-index: -1;
}
.txnsummary {
  margin: 0 0 0 0;
  padding: 0 0 0 0;
  margin: 0 10px 10px 0;
}
.txntable {
  width: 100%;
  padding: 0 0 40px 0;
  margin: 0 0 0 0;
  border-spacing: 0;
}
.txnsummhead {
  padding: 20px 0;
}
.txnheader th {
  padding: 0 0 0 0;
}
.txnfield {
  text-align: left;
  padding-left: 0.5em;
}
.txnrow:nth-child(even) {
  background-color: lightgray;
}
.throw th {
  background-color: var(--primary-color);
  color: white;
  text-align: left;
  padding-left: 10px;
}

/* About */
.about {
  position: relative;
  height: 10vh;
}
.about .card {
  border: #000 1px solid;
}
.footer {
  margin: 0 10px;
  background: var(--primary-color);
  color: #fff;
  height: 50px;
  width: 100%;
  position: fixed;
  border: 0px;
  display: flex;
  align-content: unset;
  justify-content: flex-end;
  padding: 10px 10px;
  margin: 0 0;
  bottom: 0;
}
/* Utils */
.container {
  max-width: 1100px;
  margin: 0 auto;
  overflow: auto;
  padding: 0 40px;
}

.flex {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.card {
  background-color: #fff;
  color: #333;
  border-radius: 10px;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.2);
  padding: 20px;
  margin: 10px;
}

.btn {
  display: inline-block;
  padding: 10px 30px;
  cursor: pointer;
  background: var(--primary-color);
  color: #fff;
  border: none;
  border-radius: 5px;
}
.btn-outline {
  background-color: transparent;
  border: 1px #fff solid;
}
.btn:hover {
  transform: scale(0.98);
}

body:after {
  content: '';
  display: none;
  position: fixed; /* could also be absolute */
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 1;
  background-color: red;
}

.loader {
  color: #333;
  font-size: 30px;
  width: 100%;
  margin-top: 25vh;
  text-align: center;
  position: absolute;
  top: 0;
}

.loader::after {
  content: '\2026';
  display: inline-block;
  animation: dots steps(4, end) 2s infinite;
  width: 0px;
  overflow: hidden;
  vertical-align: bottom;
}
.blurbox {
  background-color: var(--primary-color);
  color: #fff;
  position: relative;
  height: 400px;
  transition: filter 300ms ease-in;
}
.blur {
  filter: blur(2px);
  margin: 0px 0px;
  padding: 0px 0px;
  width: 100%;
  height: 400px;
  z-index: 1;
}

@keyframes dots {
  to {
    width: 1.25em;
  }
}

.lds-ring {
  display: inline-block;
  width: 80px;
  height: 80px;
  margin: 0;
  position: fixed;
  left: 45%;
  top: 40%;
}
.lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 64px;
  height: 64px;
  margin: 8px;
  border: 8px solid #fff;
  border-radius: 50%;
  animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: #757575 transparent transparent transparent;
}
.lds-ring div:nth-child(1) {
  animation-delay: -0.45s;
}
.lds-ring div:nth-child(2) {
  animation-delay: -0.3s;
}
.lds-ring div:nth-child(3) {
  animation-delay: -0.15s;
}
@keyframes lds-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
